export const cleanIds = ({ globalEnvironmentDefaults = {}, dataSourceOrgIds = [] }) => {
  const isValidId = id => dataSourceOrgIds.indexOf(id) !== -1;

  const idKeyFilter = value =>
    value.endsWith("OrganizationId") || value.endsWith("OrganizationIds");

  const defaultIdKeys = Object.keys(globalEnvironmentDefaults).filter(idKeyFilter);

  return defaultIdKeys.reduce((result, key) => {
    let cleannedValue = result[key];

    if (Array.isArray(cleannedValue)) {
      cleannedValue = cleannedValue.filter(isValidId);
    } else if (typeof cleannedValue === "string") {
      cleannedValue = isValidId(cleannedValue) ? cleannedValue : null;
    }

    return {
      ...result,
      [key]: cleannedValue
    };
  }, globalEnvironmentDefaults);
};

export const getDataSourceOrgIds = ({ environments = {} }) => {
  const orgs = [];
  Object.keys(environments).forEach(configName => {
    if (environments[configName].dataSources) {
      environments[configName].dataSources.forEach(dataSource => {
        if (dataSource.organizationId) {
          orgs.push(dataSource.organizationId);
        }
      });
    }
  });

  return orgs;
};